/* @import 'variables.css'; */

/* Index */
.Intro h2{
    font-family: var(--font-family-secondary);
    font-size: 48px;
    font-weight: 100; 
    text-align: right; 
    width: 50%; 
    margin: 37px auto 0 auto;
  }
  .Intro-article {
    width: 62%;
    height: auto;
    margin: 30px auto;
    text-align: left;
  }
  .Intro-article__text {
    margin: 80px 0;
  }
  .Intro-article__list {
    color: var(--color-yellow);
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 125px;
  }
  .Intro-article__list::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 630px;
    top: 100px;
    left: 25%;
    background-color: var(--color-yellow);
  }
  .Intro-article p button {
    width: auto;
    color: inherit;
    font-size: inherit;
    font-weight: inherit;
    background-color: inherit;
    border: inherit;
    margin: 0;
    transition: transform 0.3s ease;
  }
  .Intro-article p button:hover {
    color: white;
    transform: color;
  }
  
.avis-article h2,
.ambiance-article h2,
.news-article h2 {
  position: absolute;
  top: -250px;
  left: 50%;
  margin-block-start: 0;
  transform: translateX(-50%);
}
  
body.background1, 
body.background2, 
body.background3 {
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
body.background1 {
  background-image: url('../assets/images/image1.jpg');
}
body.background2 {
  background-image: url('../assets/images/image2.jpg');
}
body.background3 {
  background-image: url('../assets/images/image3.jpg');
}

.ambiance-text {
  color: var(--color-yellow);
  font-family: var(--font-family-secondary);
}
.ambiance-musics {
  display: flex;
  justify-content: center;
  gap: 120px;
  padding: 40px;
}
.ambiance-musics__button,
.ambiance-musics__button-radio {
  font-family: var(--font-family-secondary);
  color: white;
  font-weight: 100;
  background-color: inherit;
  width: var(--width-music-buttons);
  height: 406px;
  border-radius: 5px;
  position: relative;
}
.ambiance-musics__button-radio {
  margin-top: 30px;
}
.ambiance-musics__button p,
.ambiance-musics__button-radio p {
  margin: 0;
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  text-align: center;
}
.headphones {
  position: absolute;
  top: 20px;
  left: 10%;
}
.radio {
  width: 386px;
  position: absolute;
  bottom: 10px;
  left: -110px;
}

.headband {
  color: black;
  background-color: white;
  font-family: var(--font-family-secondary);
  font-size: 40px;
  width: 100%;
  display: flex;
  gap: 60px;
  overflow: hidden;
  white-space: nowrap;
}
.headband-text {
  animation: scrollText 20s linear infinite;
}
@keyframes scrollText {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
